import { Avatar, Card, Button, Layout, Menu, List, Skeleton } from 'antd';
import React, { useCallback, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { LOG_OUT_REQUEST } from '../reducers/user';
import Link from 'next/link';

const NetworkTab = () => {
  const { me } = useSelector(state => state.user);
  const dispatch = useDispatch();

  const onLogout = useCallback(() => {
    dispatch({
      type: LOG_OUT_REQUEST,
    });
  }, []);
  const { Sider } = Layout;

  const joblist = ['DBA', '웹/앱 개발'];
  return (
    <Sider
      trigger={<Button>Menu</Button>}
      // "rgb(0, 21, 41, 1)"
      style={{ background: "#FFFFFF", color: "#FFFFFF" }}
      breakpoint="lg"
      collapsedWidth="0"
      onBreakpoint={broken => {
        console.log(broken);
      }}
      onCollapse={(collapsed, type) => {
        console.log(collapsed, type);
      }}
    >
      <div className="logo" />
      <Menu style={{ width: '200px', color: "FFFFFF" }} theme="light" mode="inline">
        <Card
          style={{ background: "#FFFFFF" }}
        >
          <Card.Meta
            avatar={<Avatar>{me.nickname[0]}</Avatar>}
            title={<Link href="/profile"><a style={{color:"#222222"}}>{me.nickname}</a></Link>}
          />
          {/* "rgb(0, 21, 41, 0.7)"  ,color: "#FFFFFF"*/}
          <Button onClick={onLogout} style={{  background: "#FFFFFF"}}>로그아웃</Button>
        </Card>
        <Menu.Item key="1"><Link href="/friend"><a>인맥</a></Link></Menu.Item>
        <Menu.Item key="2"><Link href="/message"><a>메시지</a></Link></Menu.Item>
        <Card title="관심직무 태그" bordered={false} extra={<a href="#">+</a>}>
          <List
            className="demo-loadmore-list"
            itemLayout="horizontal"
            dataSource={joblist}
            renderItem={item => (
              <List.Item
                actions={[<a key="list-delete">x</a>]}
              >
                <List.Item.Meta
                  title={<a href="/network"><p>#DBA</p></a>}
                />

              </List.Item>
            )}
          />

        </Card>
      </Menu>
    </Sider>

  );
};

export default NetworkTab;